
import {Library} from "../../song";

export default class extends Library {
  getTitle() { return "Mohayonao Wave Library Index"; }
  getPresets() {
    const library = require("./mohayonao.library.json") || [];
    return library.samples.map(preset => {
      if(typeof preset !== "object")
        preset = {url: preset, type:'custom'};
      if(!preset.title)
        preset.title = preset.url.split('/').pop();
      if(library.baseURL)
        preset.url = library.baseURL + preset.url;
      return ['OscillatorInstrument', preset];
    });
  }
}
