import React from 'react';
import {
    Switch,
    Route,
    BrowserRouter
} from "react-router-dom";
import './App.css';

import {ASPlayer} from './audio-source/player';
import {ASComposer} from "./audio-source/composer/";
import SongProxyWebViewClient from "./audio-source/song/proxy/SongProxyWebViewClient";
// import MenuOverlayContainer from "./audio-source/components/menu/MenuOverlayContainer";


class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            // menuOpen: true,
            // menuOptions: null
        };

        // setTimeout(e => this.toggleMenu(), 200);
    }

    toggleMenu = () => {
        this.setState({ menuOpen: !this.state.menuOpen });
        if(!this.state.menuOpen)
            this.playerElm.switchToMenu(null);
    };

    updateMenu = () => {
        this.forceUpdate();
    };

    componentDidMount() {
    }

    renderDrawerContent() {
        // let content = <Text>Close</Text>;
        // if(this.playerElm)
        //     content = this.playerElm.renderMenu();
        // return (
        //     <TouchableOpacity onPress={this.toggleMenu}>
        //         <View style={styles.menuContainer}>{content}</View>
        //     </TouchableOpacity>
        // );
    }

    // render() {
    //     return <SongProxyWebViewClient/>;
    // }

    render() {


        return (
            <BrowserRouter>
                <Switch>
                    <Route path={['/blank', '/proxy']}>
                        <SongProxyWebViewClient/>
                    </Route>
                    <Route path={['/player', '/p']}>
                        {this.renderPlayer()}
                    </Route>
                    <Route path={['/composer', '/c']}>
                        {this.renderComposer()}
                    </Route>
                    <Route path={['/both', '/b']}>
                        {this.renderComposer()}
                        {this.renderPlayer()}
                    </Route>
                    <Route path="/"
                        render={(props) => {
                            switch(props.location.search) {
                                case '?blank':
                                case '?proxy':
                                    return <SongProxyWebViewClient/>;
                                default:
                                    return this.renderComposer();
                            }
                        }}
                    >
                    </Route>
                </Switch>
            </BrowserRouter>
        );
    }

    renderPlayer() {
        return (
            <ASPlayer
                src="files/test.pl.json"
                ref = {ref => this.playerElm = ref}
                onToggleMenu={this.toggleMenu}
                onUpdateMenu={this.updateMenu}
            />
        );
    }

    renderComposer() {
        return (
            <ASComposer
                // src="files/demo.json"
                ref = {ref => this.composer = ref}
                onToggleMenu={this.toggleMenu}
                onRenderMenu={e => this.menu.onRenderMenu}
            />
        );
    }
}

export default App;
